<template>
  <div id="app" :class="{ 'dark-mode': isDarkMode }">
    <div class="theme-toggle">
      <button @click="toggleTheme" class="theme-button">
        {{ isDarkMode ? '☀️' : '🌙' }}
      </button>
    </div>

    <nav class="navigation">
      <button 
        v-for="(component, name) in availableComponents" 
        :key="name"
        @click="toggleComponent(name)"
        :class="{ active: activeComponents.includes(name) }"
      >
        {{ component.title }}
      </button>
    </nav>

    <div class="components-container">
      <transition-group name="component-fade">
        <component 
          v-for="name in activeComponents" 
          :key="name"
          :is="availableComponents[name].component"
          class="component-item"
        ></component>
      </transition-group>
    </div>
  </div>
</template>

<script>
import ScheduleView from './views/Schedule.vue'
import Grades from './views/Grades.vue'
import Homework from './views/Homework.vue'
import AdminPanel from './views/AdminPanel.vue'
import ManageLesson from './views/ManageLesson.vue'

export default {
  name: 'App',
  components: {
    Schedule: ScheduleView,
    Grades,
    Homework,
    AdminPanel,
    ManageLesson
  },
  data() {
    return {
      activeComponents: [],
      availableComponents: {
        schedule: { 
          component: 'Schedule',
          title: 'Расписание'
        },
        grades: { 
          component: 'Grades',
          title: 'Оценки'
        },
        homework: { 
          component: 'Homework',
          title: 'Домашние задания'
        },
        admin: { 
          component: 'AdminPanel',
          title: 'Админ панель'
        },
        manageLesson: { 
          component: 'ManageLesson',
          title: 'Управление уроками'
        }
      },
      isDarkMode: false
    }
  },
  methods: {
    toggleComponent(name) {
      const index = this.activeComponents.indexOf(name)
      if (index === -1) {
        this.activeComponents.push(name)
      } else {
        this.activeComponents.splice(index, 1)
      }
    },
    checkDarkMode() {
      const hour = new Date().getHours()
      if (hour >= 17 || hour < 6) {
        this.isDarkMode = true
        document.documentElement.classList.add('dark-mode')
      } else {
        this.isDarkMode = false
        document.documentElement.classList.remove('dark-mode')
      }
    },
    toggleTheme() {
      this.isDarkMode = !this.isDarkMode
      localStorage.setItem('darkMode', this.isDarkMode)
      if (this.isDarkMode) {
        document.documentElement.classList.add('dark-mode')
      } else {
        document.documentElement.classList.remove('dark-mode')
      }
    }
  },
  mounted() {
    const savedTheme = localStorage.getItem('darkMode')
    if (savedTheme !== null) {
      this.isDarkMode = savedTheme === 'true'
      if (this.isDarkMode) {
        document.documentElement.classList.add('dark-mode')
      }
    } else {
      this.checkDarkMode()
    }
    setInterval(this.checkDarkMode, 60000)
  }
}
</script>

