<template>
  <div class="login-container">
    <form @submit.prevent="handleSubmit" class="login-form">
      <h1>Вход</h1>
      
      <div class="form-group">
        <input 
          type="text" 
          placeholder="Введите логин" 
          id="username" 
          v-model="username" 
          required
        >
        <label for="username">Логин</label>
        <i class="material-icons icon">mail</i>
      </div>

      <div class="form-group">
        <input 
          type="password" 
          placeholder="Введите пароль" 
          id="password" 
          v-model="password" 
          required
        >
        <label for="password">Пароль</label>
        <i class="material-icons icon">lock</i>
      </div>

      <button type="submit">Войти</button>
      <p><a href="#">Забыл пароль?</a></p>

      <!-- Сообщение об ошибке -->
      <p v-if="error" class="error-message">{{ error }}</p>
    </form>
  </div>
</template>

<script>
export default {
  name: 'LoginForm',
  data() {
    return {
      username: '',
      password: '',
      error: null
    }
  },
  methods: {
    async handleSubmit() {
      try {
        const response = await fetch('/api/login', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({
            username: this.username,
            password: this.password
          })
        });

        const data = await response.json();

        if (response.ok) {
          // Сохраняем данные пользователя
          localStorage.setItem('userRole', data.role);
          localStorage.setItem('userName', data.username);
          
          // Перенаправляем на главную страницу
          this.$router.push('/schedule');
        } else {
          this.error = data.message || 'Ошибка входа';
        }
      } catch (err) {
        this.error = 'Ошибка сервера';
      }
    }
  }
}
</script>

<style scoped>
.login-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background: radial-gradient(circle, #362f58, #2a546a, #2f2f2f, #6b86ff);
  animation: backgroundAnimation 10s infinite alternate;
  background-size: 300% 300%;
}

@keyframes backgroundAnimation {
  0% { background-position: 0% 50%; }
  100% { background-position: 100% 50%; }
}

.login-form {
  background: rgba(255, 255, 255, 0.9);
  padding: 40px;
  border-radius: 10px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
  width: 100%;
  max-width: 400px;
}

h1 {
  text-align: center;
  color: #333;
  margin-bottom: 30px;
}

.form-group {
  position: relative;
  margin-bottom: 25px;
}

input {
  width: 100%;
  padding: 10px 40px 10px 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 16px;
  transition: border-color 0.3s;
}

input:focus {
  outline: none;
  border-color: #4CAF50;
}

label {
  position: absolute;
  top: -8px;
  left: 10px;
  background: white;
  padding: 0 5px;
  font-size: 12px;
  color: #666;
}

.icon {
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
  color: #666;
}

button {
  width: 100%;
  padding: 12px;
  background: #4CAF50;
  color: white;
  border: none;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s;
}

button:hover {
  background: #45a049;
}

a {
  color: #4CAF50;
  text-decoration: none;
  font-size: 14px;
}

a:hover {
  text-decoration: underline;
}

p {
  text-align: center;
  margin-top: 15px;
}

.error-message {
  color: #dc3545;
  margin-top: 15px;
  text-align: center;
  font-size: 14px;
}

@media (max-width: 480px) {
  .login-form {
    margin: 20px;
    padding: 20px;
  }
}
</style> 