import { createRouter, createWebHistory } from 'vue-router'
import LoginForm from '../views/LoginForm.vue'

// Импортируем компоненты для маршрутизации
const Schedule = () => import('../views/Schedule.vue')
const Grades = () => import('../views/Grades.vue')
const Homework = () => import('../views/Homework.vue')
const ManageLesson = () => import('../views/ManageLesson.vue')
const AdminPanel = () => import('../views/AdminPanel.vue')

const routes = [
  {
    path: '/',
    redirect: '/login'
  },
  {
    path: '/login',
    name: 'Login',
    component: LoginForm,
    meta: { 
      title: 'Вход в систему',
      public: true
    }
  },
  {
    path: '/schedule',
    name: 'Schedule',
    component: Schedule,
    meta: { title: 'Расписание' }
  },
  {
    path: '/grades',
    name: 'Grades',
    component: Grades,
    meta: { title: 'Оценки' }
  },
  {
    path: '/homework',
    name: 'Homework',
    component: Homework,
    meta: { title: 'Домашние задания' }
  },
  {
    path: '/manage-lesson',
    name: 'ManageLesson',
    component: ManageLesson,
    meta: { 
      title: 'Управление уроками',
      requiresTeacher: true 
    }
  },
  {
    path: '/admin',
    name: 'AdminPanel',
    component: AdminPanel,
    meta: { 
      title: 'Админ панель',
      requiresAdmin: true 
    }
  }
]

const router = createRouter({
  history: createWebHistory(),
  routes
})

router.beforeEach((to, from, next) => {
  const isAuthenticated = !!localStorage.getItem('userRole')
  
  // Устанавливаем заголовок страницы
  document.title = to.meta.title || 'Учебный портал'
  
  if (!isAuthenticated && !to.meta.public) {
    // Перенаправляем на страницу логина
    next('/login')
  } else if (isAuthenticated && to.path === '/login') {
    // Если пользователь авторизован и пытается зайти на страницу логина
    next('/schedule')
  } else {
    next()
  }
})

export default router 